.messageSender {
    display: flex;
    margin-top: 30px;
    flex-direction: column;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 5px 7px -7px rgba(0,0,0,0.75);
    width: 100%;
}

.messageSender__top {
    display: flex;
    border-bottom: 1px solid #eff2f5;
    padding: 15px;
    align-items: center;

}
.messageSender__top > form {
    flex:1;
    display: flex;
}

.messageSender__top > form > input {
    outline-width: 0;
    border: none;
    padding: 15px 20px;
    margin: 0 10px;
    border-radius: 999px;
    background-color: #eff2f5;

}

/* we can post messages by clicking enter on laptop devices */
.messageSender__top > form > button {
    display: none;

}

/* the post button is visible on mobile devices */
@media (max-width:1200px) {
    .messageSender__top > form > button {
        display: visible;
    }
}

.messageSender__input {
    flex:1;
}


.messageSender__bottom {
    display: flex;
   justify-content: space-evenly;

}

.messageSender__option {
    padding: 20px ;
    display: flex;
    align-items: center;
    color: gray;
    margin: 2px;
    cursor: pointer;

}

.messageSender__option >h3{
    font-size: large;
    margin-left: 10px;

}

.messageSender__option:hover {
    background-color: #eff2f5;
    border-radius: 10px;
    
}